body,
html {
    height: 100%
}

body {
    background: #d3d3d3;
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    color: #000
}

.wrapper {
    margin: 0 auto;
    padding: 1em
}

h1 {
    margin: 1em .5em
}

h1,
section {
    color: #063754
}

section {
    background: #708090;
    margin: 1em;
    padding: 1em
}

section .col {
    display: flex;
    flex-direction: row
}

section .col>div {
    flex-grow: 1;
    flex-basis: 0
}

section .col>div>a button {
    float: right
}